$headColor: rgb(24, 92, 27);
$buttonColor: rgb(15, 2, 75);

:root {
font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
margin: 0px;
padding: 0px;
}

body {
    background-image: linear-gradient(to bottom, rgb(144, 156, 75),rgb(229, 241, 157),rgb(144, 156, 75),green,rgb(144, 156, 75),yellow,rgb(144, 156, 75))
}

.main-container {
display: flex;
justify-content: space-between;
}

p {
margin: 10px;
}

.sub-container {
display: flex;
flex-direction: column;
width: 300px;
height: 100%;
font-size: 20px;
border: black solid 1px;
border-radius: 5px;
padding: 0px 0px 10px 0px;
box-shadow: 1px 1px black;
// text-shadow: 1px 1px;
}    

.sub-container-1 {
margin: 0px 10px 10px 10px;
border: black solid 1px;
padding: 10px;
box-shadow: 2px 2px black;
}

.head-container {
border: black solid 1px;
background-color: red;
margin: 0px;
padding: 0px;
}
.title-container {
text-shadow: 1px 1px;
margin: 10px 10px 0px 10px;
border: black solid 1px;
border-top-left-radius: 5px;
border-top-right-radius: 5px;
text-align: center;
box-shadow: 2px 2px black;
}

@for $i from 1 through 4 {
.head-container-#{$i} {
    text-shadow: 1px 1px;
    text-align: center;
    background-color: #231 * $i;
    color: white;
}
}

button {
text-shadow: 1px;
background-color: $buttonColor;
width: 100px;
height: 30px;
border-radius: 5px;
color: white;
}

/* The Close Button */
.close {
color: #aaa;
float: right;
font-size: 28px;
font-weight: bold;
}

.nav-container .close:hover,
.nav-container .close:focus {
color: black;
text-decoration: none;
cursor: pointer;
}

.nav-container ul {
list-style-type: none;
margin: 10;
padding: 0;
overflow: hidden;
background-color: rgb(0, 0, 0);
opacity: 0.8;
}


.nav-container li {
float: left;
border-right:1px solid #bbb;

}

.nav-container li:last-child {
border-right: none;
}

.nav-container li a {
display: block;
color: white;
text-align: center;
padding: 14px 16px;
text-decoration: none;
}

.nav-container li a:hover:not(.active) {
background-color: #111;
}

.nav-container .active {
background-color: #4CAF50;
}

.container {
padding: 16px;
}

ol li{
list-style:none;
}

/* The Modal (background) */
.modal {
display: none; /* Hidden by default */
position: fixed; /* Stay in place */
z-index: 1; /* Sit on top */
left: 0;
top: 0;
width: 100%; /* Full width */
height: 100%; /* Full height */
overflow: auto; /* Enable scroll if needed */
background-color: rgb(0,0,0); /* Fallback color */
background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
background-color: #fefefe;
margin: 15% auto; /* 15% from the top and centered */
padding: 20px;
border: 1px solid #888;
width: 80%; /* Could be more or less, depending on screen size */
}

/* The Close Button */
.close {
color: #aaa;
float: right;
font-size: 28px;
font-weight: bold;
}

